import SystemConst from "./systemConst";

namespace CanvasUtil {

    /**
     * Canvasに任意の描画をし、画像のURLを取得する
     * @param width 幅
     * @param height 高さ
     * @param draw 描画
     * @returns 画像のURL
     */
    export const createURL = (width: number, height: number, draw: (ctx: CanvasRenderingContext2D) => void) => {
        const canvas = document.createElement('canvas');
        const ctx = canvas.getContext('2d') as CanvasRenderingContext2D;
        canvas.width = width;
        canvas.height = height;
        draw(ctx);
        return canvas.toDataURL();
    }

    export const getTitleURL = () => {
        return CanvasUtil.createURL(360, 80, (ctx) => {
            // ctx.fillStyle = '#ffffff';
            // ctx.fillRect(0, 0, 350, 80);

            ctx.shadowColor = "#555";
            ctx.shadowOffsetX = 2;
            ctx.shadowOffsetY = 1;
            ctx.shadowBlur = 5;
            ctx.font = 'italic 50pt Arial';
            ctx.fillStyle = '#c6dce2';
            ctx.fillText('Pixel ', 14, 64);
            const adjust = ctx.measureText('Pixel ').width;
            ctx.fillStyle = '#768fc4';
            ctx.fillText('Flare', 14 + adjust, 64);
            ctx.save();
        });
    }

    export const getLogoURL = () => {
        const WIDTH = 512;
        return CanvasUtil.createURL(WIDTH, WIDTH, (ctx) => {
            // ctx.fillStyle = '#ffffff';
            // ctx.fillRect(0, 0, 350, 80);

            // ctx.fillStyle = '#35304b';
            // ctx.fillRect(0, 0, WIDTH, WIDTH);
            // ctx.fillStyle = '#ffffff';
            // ctx.strokeRect(0, 0, WIDTH, WIDTH);

            ctx.beginPath();
            const centerX = WIDTH / 2;
            const centerY = WIDTH / 2;
            const rad = 55;
            const g = ctx.createRadialGradient(centerX, centerY, 1, centerX, centerY, rad);
            g.addColorStop(0, '#942e7a');
            g.addColorStop(1, '#530640');
            ctx.fillStyle = g;
            ctx.arc(centerX, centerY,rad , 0, 2 * Math.PI);
            ctx.fill();

            for (let i = 0; i < 8; i++) {
                const r = Math.PI * 2 / 8 * i;
                const d = 90;
                const x = WIDTH / 2 + Math.cos(r) * d;
                const y = WIDTH / 2 + Math.sin(r) * d;
                const rad = 30;
                const g = ctx.createRadialGradient(x, y, 1, x, y, rad);
                g.addColorStop(0, '#fffefe');
                g.addColorStop(1, '#d1d1d1');
                ctx.fillStyle = g;
                ctx.beginPath();
                ctx.arc(x, y, rad, 0, 2 * Math.PI);
                ctx.fill();
            }

            for (let i = 0; i < 4; i++) {
                const r = Math.PI * 2 / 4 * i;
                const d = 176;
                const x = WIDTH / 2 + Math.cos(r) * d;
                const y = WIDTH / 2 + Math.sin(r) * d;
                const rad = 45;
                const g = ctx.createRadialGradient(x, y, 1, x, y, rad);
                g.addColorStop(0, '#fffefe');
                g.addColorStop(1, '#d1d1d1');
                ctx.fillStyle = g;
                ctx.beginPath();
                ctx.arc(x, y, rad, 0, 2 * Math.PI);
                ctx.fill();
            }
            ctx.save();
        });
    }

    export const getAuthTextURL = (text: string) => {
        const WIDTH = SystemConst.AUTH_TEXT_WIDTH;
        const SIZE = SystemConst.AUTH_TEXT_WIDTH / 6;
        return createURL(WIDTH, SystemConst.AUTH_TEXT_HEIGHT, (ctx) => {
            ctx.rotate(1 * Math.PI / 180);
            for (let i = 0; i < text.length; i++) {
                const char = text.charAt(i);

                const fontRand = Math.random() * 60;
                const alphaRand = Math.random() * 60;
                ctx.font = `${20 + fontRand}pt Arial`;
                ctx.fillStyle = "rgba(" + [40, 0, 100, 0.05 + 0.01 * alphaRand] + ")";
                ctx.fillText(char, i * SIZE, 60);
            }
        });
    }
}

export default CanvasUtil;